import React, { Component } from 'react';
import { withRouter } from 'react-router';

// styles icons 
import {Row, Col } from 'react-bootstrap';
import search from '../../images/instractor-home/searching.svg';
import student from '../../images/instractor-home/network.svg';
import coins from '../../images/instractor-home/coins.svg';

// language for test
import text from '../../languages/english/translate.json';

class Welcome extends Component {
    componentWillMount() {
    }
    render() {
        let data = text.instractorHome.dashboard;
        return (
            <div >
                <div>
                    <p className="mb-0">{data.hi}</p>
                    <h3 className="text-green bold">{data.welcome}</h3>
                </div>
                <Row className="mt-4">
                    <Col md="3">
                        <div className="shadow mt-2  p-3 row m-0 mb-4">
                            <Col xs={6} className="p-0 text-left"><h3 className="bold">230K </h3></Col>
                            <Col xs={6} className="p-0 text-right"><img src={search} className="img-fluid text-right" alt="search icon"/></Col>
                            <p className="mb-0">{data.viewNumber}</p>
                        </div>
                    </Col>
                    <Col md="3">
                        <div className="shadow mt-2  p-3 row m-0 mb-4">
                            <Col xs={6} className="p-0 text-left"><h3 className="bold">230K </h3></Col>
                            <Col xs={6} className="p-0 text-right"><img src={student} className="img-fluid text-right" alt="search icon"/></Col>
                            <p className="mb-0">{data.studentNumer}</p>
                        </div>
                    </Col>
                    <Col md="3">
                        <div className="shadow mt-2  p-3 row m-0 mb-4">
                            <Col xs={6} className="p-0 text-left"><h3 className="bold">230K </h3></Col>
                            <Col xs={6} className="p-0 text-right"><img src={coins} className="img-fluid text-right" alt="search icon"/></Col>
                            <p className="mb-0">{data.earnings}</p>
                        </div>
                    </Col>
                </Row>
            </div>
        )
    }
}

export default withRouter(Welcome)