import React, { Component } from 'react';
import { withRouter } from 'react-router';

// styles icons 
import { Row, Col } from 'react-bootstrap';
import dots from '../../images/instractor-home/more.svg';
import clock from '../../images/instractor-home/vip.svg';

// language for test
import text from '../../languages/english/translate.json';

class TodaySessions extends Component {
    componentWillMount() {
    }
    render() {
        let data = text.instractorHome.dashboard;
        return (
            <div >
                <div className="rounded border m-0 p-3">
                    <div>
                        <h5>{data.todaySession.title}</h5>
                        <p className="text-grey">{data.todaySession.description}</p>
                    </div>
                    <div className="border-top pt-3">
                        <Row className="mb-2 align-items-center">
                            <Col md={3}>
                                <img src="https://via.placeholder.com/150" className="rounded-circle img-fluid" alt="profile" />
                            </Col>
                            <Col md={6} className="p-0">
                                <div>
                                    <p className="mb-0">Titus Deckow</p>
                                    <div>
                                        <p className="mb-0">
                                            <span className="mr-3">Tahfeez</span>
                                            <span><img src={clock} className="img-fluid pr-1 pl-1" alt="clock"/>3:53 AM</span>
                                        </p>
                                    </div>
                                </div>
                            </Col>
                            <Col md={3} className="text-right">
                                <button className="bg-transparent border-0">
                                    <img src={dots} className="img-fluid" alt="clock icon"/>
                                </button>
                            </Col>
                        </Row>
                        <Row className="mb-2 align-items-center">
                            <Col md={3}>
                                <img src="https://via.placeholder.com/150" className="rounded-circle img-fluid" alt="profile" />
                            </Col>
                            <Col md={6} className="p-0">
                                <div>
                                    <p className="mb-0">Titus Deckow</p>
                                    <div>
                                        <p className="mb-0">
                                            <span className="mr-3">Tahfeez</span>
                                            <span><img src={clock} className="img-fluid pr-1 pl-1" alt="clock"/>3:53 AM</span>
                                        </p>
                                    </div>
                                </div>
                            </Col>
                            <Col md={3} className="text-right">
                                <button className="bg-transparent border-0">
                                    <img src={dots} className="img-fluid" alt="clock icon"/>
                                </button>
                            </Col>
                        </Row>
                    </div>
                </div>
            </div>
        )
    }
}

export default withRouter(TodaySessions)