import React,{Component} from 'react';
import {Link} from 'react-router-dom';

export default class HomeInstractor extends Component{
    render(){
        return(
            <div>
                <h3 className="w-100 text-center pt-4">Home Student.
                    <Link 
                    to="/"
                    onClick={e=>{
                        localStorage.clear();
                    }}
                    className="border-0 pl-3 text-grey small">Logout</Link>
                </h3>

            </div>
        )
    }
}