import React from 'react';
import {Link} from 'react-router-dom';

// styles and images
import { Navbar, Nav } from 'react-bootstrap';
import {Avatar} from '@material-ui/core'
import logoWeb from '../../images/logo/logo-1.svg';

// language for test
import text from '../../languages/english/translate.json';

export default function Header() {
    let data = text?.home.header;
    return (
        <Navbar collapseOnSelect  expand="lg" bg="white" className="shadow" variant="light">
                <Navbar.Brand className="pr-5" href="#intro">
                    <img src={logoWeb} className="img-fluid" alt="logo" />
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                    <Navbar.Collapse id="responsive-navbar-nav">
                        <Nav   className=" mr-auto ">
                            <Nav.Link  className="pr-3 pl-3 active-header">
                                dashboard
                            </Nav.Link>
                            <Nav.Link className="pr-3 pl-3">
                                Earnings
                            </Nav.Link>
                            <Nav.Link className="pr-3 pl-3">
                                Sessions
                            </Nav.Link>
                            <Nav.Link className="pr-3 pl-3">
                                Messages
                            </Nav.Link>
                            <Nav.Link className="pr-3 pl-3">
                                Notifications
                            </Nav.Link>
                        </Nav>
                        <Nav className="align-items-center">
                            <Nav.Link>
                                <Avatar  alt="Remy Sharp"  />
                            </Nav.Link>
                            <Nav.Link>
                                <Link  
                                    to="/"
                                    onClick={e=>{
                                        localStorage.clear();
                                    }}>
                                Logout</Link>
                            </Nav.Link>
                        </Nav>
                    </Navbar.Collapse>
        </Navbar>
    )
}