import React, { Component } from 'react';
import $ from 'jquery';
// styles 
import { Row, Col , Table} from 'react-bootstrap';
import Loader from 'react-loader-spinner';
import './calendar.style.css';

// language for test
import text from '../../languages/english/translate.json';

export default class Calendar extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedRow : 1,
            loading : false,
            week: 1,
            year: 2020,
            month: 10,
            reservedSession : [],
            reservedSlots : []
        }
    }
    componentDidMount() {
        this.showCalendar(this.state.year, this.state.month);
        this.handleRowSelect();
        this.showClendarBerWeek(1);
        this.getCalendarData();
        this.handelCellSelect();
        let x = this.showReservedSlots( 3 , "1PM");
    }
    componentWillReceiveProps(nextProps){
        if(nextProps.refreshData){
            this.getCalendarData()
        }
    }
    times = ["8AM", "9AM", "10AM", "11AM", "12PM", "1PM","2PM", "3PM", "4PM", "5PM", "6PM", "7PM",
            "8PM", "9PM", "10PM","11PM"];
    months = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"];
    // calendar functions
    // get days of a month
    daysInMonth(iYear, iMonth) {
        return 32 - new Date(iYear, iMonth, 32).getDate();
    }
    getFirstDayOfWeek(row){
        let day = 0;
        row = row + 2;
        day = document.querySelector('.month-table tr:nth-child('+row+') td')?.innerHTML
        return day;
    }
    convertTimeFrom12To24(time) {
        if (time?.includes('AM')) {
            // from 1 to 9 AM
            if (parseInt(time.substring(0, 2)) <= 9) {
                return '0' + time.replace('AM', ':00:00')
            }
            // 12 AM
            else if (parseInt(time.substring(0, 2)) === 12) {
                return '00:00:00';
            }// 10 and 11 AM 
            else {
                return time.replace('AM', ':00:00')
            }
        }
        else if(time?.includes('PM')) {
            // 12 PM
            if (parseInt(time.substring(0, 2)) === 12) {
                let parseTime = parseInt(time.substring(0, 2));
                return parseTime + ':00:00';
            }
            // from 1 to 11 PM
            else {
                let parseTime = parseInt(time.substring(0, 2)) + 12;
                return parseTime + ':00:00';
            }
        }
    }
    convertTimeFrom24To12(time){
        let convertedTime = time.substring(0,2);

        if(convertedTime > 12 )
            convertedTime = (convertedTime-12)+'PM' 

        else if(parseInt(convertedTime) === 12 )
            convertedTime = convertedTime+'PM' 

        else convertedTime =parseInt(convertedTime[0]) === 0? convertedTime[1]+'AM' :  convertedTime+'AM';

        return convertedTime;
    }// end of calendar functions

    //show calendar of a month
    showCalendar(year, month) {
        let firstDay = new Date(year, month).getDay();

        let date = 1;
        for (let i = 0; i < 5; i++) {
            let row = document.createElement("tr"), cell, cellText;

            for (let j = 0; j < 7; j++) {
                if (i === 0 && j < firstDay) {
                    cell = document.createElement("td");
                    cellText = document.createTextNode("");
                    cell.appendChild(cellText);
                    row.appendChild(cell);
                }
                else if (date > this.daysInMonth(year, month)) {
                    break;
                }
                else {
                    cell = document.createElement("td");
                    cellText = document.createTextNode(date);
                    cell.appendChild(cellText);
                    row.appendChild(cell);
                    date++;
                }
            }
            document.querySelector('.month-table').append(row);
        }
    }
    // show calendar ber week
    showClendarBerWeek(week, times = this.times) {
        if(week !== 0){
            // cleate week table
            if (document.querySelector('.week-table tr')) {
                document.querySelectorAll('.week-table tr').forEach(tr => tr.remove());
            }

            let i = 2 + week;
            let cell, cellText;
            cell = document.createElement("td");
            cellText = document.createTextNode("");
            cell.appendChild(cellText);

            let row = document.querySelector('.month-table  tr:nth-child(' + i + ')');
            let rowCopy = row.cloneNode(true);
            rowCopy.prepend(cell);
            document.querySelector('.week-table').append(rowCopy);

            for (let i = 0; i < times.length; i++) {
                let row = document.createElement("tr");
                for (let j = 0; j < 8; j++) {
                    if (j === 0) {
                        cell = document.createElement("td");
                        cellText = document.createTextNode(times[i]);
                        cell.append(cellText);
                        row.append(cell);
                    }
                    else {
                        cell = document.createElement("td");
                        cellText = document.createTextNode("");
                        cell.append(cellText);
                        row.append(cell);
                    }

                }
                document.querySelector('.week-table').append(row);
            }
        }
    }
    // when user select a week from the months calendar
    handleRowSelect() {
        let that = this;

        const row = document.querySelectorAll(".month-table tr");
        for (let i = 0; i < row.length; i++) {
            row[i].addEventListener("click", function () {
                let copyRow = i;
                that.setState({...that.state , selectedRow : copyRow})
                that.getCalendarData();
            });
        }

    }
    // when user select a day and time from weeks calendar 
    handelCellSelect() {
        let that = this;
        const cell = document.querySelectorAll(".week-table td");

        for (let i = 0; i < cell.length; i++) {
            cell[i].addEventListener("click", function () {
                //check if there is a reserved session or not
                if(cell[i].getAttribute('data-id') && cell[i].classList.contains('bg-lightGreen')){
                    // handle show data of reserved slots
                    let id = cell[i].getAttribute('data-id');
                    let slot = that.state.reservedSlots.filter(slot => parseInt(slot.id) === parseInt(id))[0];
                    that.handleShowReservedSlotDetails(slot);
                }
                else if(cell[i].getAttribute('data-id') && cell[i].classList.contains('bg-green')){
                    // handle show data of reserved session
                }
                else{   
                    let rowIndex = this.parentNode.rowIndex;
                    let cellIndex = this.cellIndex;
                    let daysRow = document.querySelectorAll(".week-table tr")[0]
                    let day = daysRow.querySelectorAll("td")[cellIndex]?.innerHTML;
                    let time = that.times[rowIndex - 1]
                    
                    that.handleReserveSlot(day,that.convertTimeFrom12To24(time))
                }
            })
        }
    }
    // reserve a slot popUp
    handleReserveSlot(day , time){
        if(day && time){
            let date =  this.state.year +"-"+ (this.state.month + 1)+"-"+ day;
            this.props.reserveSlot(date,time)
        }
    }
    // get all reserved slots and sessions 
    getCalendarData () {
        let that = this;
        let row = this.state.selectedRow;
        let firstDay = that.getFirstDayOfWeek(row);
        if(firstDay){
            let startingDate = that.state.year+'-'+(that.state.month+1)+'-'+firstDay
            console.log( JSON.parse(localStorage.getItem('jwt')))
            const token = (localStorage.getItem('jwt')) ? JSON.parse(localStorage.getItem('jwt')).BearerTokenResponse?.access_token : JSON.parse(localStorage.getItem('jwt')).access_token ;
            console.log(token)
            if (!token) {
                that.props.history.push({
                    pathname: '/login'
                })
            }
            else{
                that.setState({...this.state , loading : true})
                $.ajax({
                    url: 'https://quranacademy.black-squares.com/graphql',
                    method: 'post',
                    headers: {
                        authorization: `Bearer ${token}`
                    },
                    data: {
                        query: `
                        query($afterDate : Date){
                            meInstructors{
                            id
                            sessionsByUnits(afterDate:$afterDate, firstUnits: 7, 				hasInUnits: dayly , is_confirmed  : false){
                                status{
                                counter
                                counterConfirmed
                                }
                                sessions{
                                id
                                date
                                time
                                rate_minutes_type{
                                    id
                                    name
                                }
                                course_type{
                                    id
                                    name
                                }
                                student{
                                    id
                                    first_name
                                }
                                }
                            }
                            }
                        }
                            `,
                        variables: {
                            afterDate : startingDate
                        }
                    },
                    dataType: 'json',
                    success: res => {
                            that.setState({...this.state , loading : false})
                        if(res.data?.meInstructors?.sessionsByUnits){
                            that.filterCalendarData(res.data.meInstructors.sessionsByUnits , row)
                        }
                    },
                    cache: err => console.log(err)
                });
            }
        }
    }
    // split data into reserved slots and reserved sessions
    filterCalendarData(data, row) {
        let that = this, reservedSlots = [], reservedSession = [];
        data.filter(reserved => {
            if (reserved.sessions.length > 0) {
                if (reserved.sessions.student) {
                        reserved.sessions.forEach(session => reservedSession.push(session))
                }
                else {
                    reserved.sessions.forEach(session => reservedSlots.push(session))
                }
            }
        })
        this.setState({...this.state , reservedSession :  reservedSession, reservedSlots :  reservedSlots});
        that.showClendarBerWeek(row, that.times)
        that.handelCellSelect();
        this.handleReservedSlots();
    }
    // handle show reserved slots and sessions on week calendar
    showReservedSlots(day,time){
        let rows = document.querySelectorAll('.week-table tr');
        let rowIndex ,  columnIndex , cell ;

        rows[0].querySelectorAll('td').forEach(td=> {
            if( !isNaN(parseInt(td.innerHTML))  && parseInt(td.innerHTML) === parseInt(day)){
                columnIndex = td.cellIndex;
            }
        })
         rows.forEach(row => {
            if(row.querySelector('td').innerHTML === time){
                rowIndex = row.rowIndex;
            }
        })
        rowIndex = rowIndex + 3;
        columnIndex = columnIndex + 1;
        if(rowIndex && columnIndex){
            cell = document.querySelector('.week-table tr:nth-child('+rowIndex+') td:nth-child('+columnIndex+') ');
        }
        return  cell;

    }
    // handle data before shoing reserved slots and sessions on week calendar
    handleReservedSlots(){
        let that = this;
        let reservedSession = this.state.reservedSession;
        let reservedSlots = this.state.reservedSlots;
        reservedSlots && reservedSlots.map(slot=>{
            let day , time , cell;

            time = that.convertTimeFrom24To12(slot.time);
            day = slot.date.substring(8,10) <= 9 ? slot.date.substring(9,10) : slot.date.substring(8,10);

            cell = that.showReservedSlots(day,time);
            if(cell){
                cell.setAttribute('data-id',slot.id);
                cell.classList.add('bg-lightGreen');
            }
        })
    }
    // show slot details popUp
    handleShowReservedSlotDetails(slot){
        if(slot){
            this.props.showSlotDetails(slot)
        }
    }
    render() {
        let data = text.instractorHome.dashboard
        return (
            <div className="mt-3 calendar ">
                <div>
                    <Loader
                        className="loading"
                        visible={this.state.loading}
                        type="TailSpin"
                        color="white"
                        height={50}
                        width={50}
                    />
                </div>
                <Row className="text-right justify-content-center align-items-top ">
                    <Col md={3} className="p-0">
                        <div className=" p-0 bg-transperant">
                            <h3 className="bg-grey text-green text-center  bold border mb-0 pt-2 pb-2">
                                <span> {this.months[this.state.month]}</span> <span>{this.state.year}</span>
                            </h3>
                            <Table responsive striped bordered hover
                                className="text-center month-table mb-0" size="sm">
                                <thead className="bg-lightGreen text-green bold">
                                    <tr>
                                        <td>Su</td>
                                        <td>Mo</td>
                                        <td>Tu</td>
                                        <td>We</td>
                                        <td>Th</td>
                                        <td>Fr</td>
                                        <td>Sa</td>
                                    </tr>
                                </thead>
                                <tbody className="">
                                </tbody>
                            </Table>
                        </div>
                    </Col>
                    <Col md={9} className="">
                        <div 
                            className="border-top border-bottom " 
                            style={{ maxHeight: '35rem', overflow: 'auto', scrollBehavior: 'smooth' }}
                        >
                            <Table responsive className="text-center week-table border-0 p-0 m-0" bordered size="lg">
                                <thead className="bg-lightGreen text-green bold">
                                    <td>Time</td>
                                    <td>Sun</td>
                                    <td>Mon</td>
                                    <td>Tus</td>
                                    <td>Wed</td>
                                    <td>Thu</td>
                                    <td>Fri</td>
                                    <td>Sat</td>
                                </thead>
                                <tbody className="">
                                </tbody>
                            </Table>
                        </div>
                    </Col>
                </Row>
            </div>
        )
    }
}