import React from 'react';

// styles
import { Modal , Row , Col} from 'react-bootstrap';
import { Button } from '@material-ui/core';


export default function PopUp(props){
    return(
        <div>
            <Modal
                size="md"
                centered
                rounded
                className="row m-0  pr-0"
                show={props.show}
                onHide={e=>props.hide()}>
                    <Modal.Header closeButton className="pr-3 pl-3 pt-2 pb-2 bg-lightGreen">
                        <Modal.Title className='h5'>{props.title}</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                            {props.content}
                    </Modal.Body>
                    <Modal.Footer>
                        <Row className="w-100">
                            <Col className="text-left p-0">
                                <Button 
                                    onClick={e=>props.cancelFun()} 
                                    className="btn-green-outline pr-4 pl-4">{props.cancelBtn}</Button>
                            </Col>
                            <Col className="text-right p-0">
                                <Button  
                                    type="submit"
                                    onClick={e=>props.saveFun()}
                                    className="btn-green pr-4 pl-4">{props.saveBtn}</Button>
                            </Col>
                        </Row>
                    </Modal.Footer>
            </Modal>
        </div>
    )
}

